@import url("https://fonts.googleapis.com/css2?family=Chakra+Petch:wght@300;400&display=swap");
*,
::after,
::before {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

:root {
    --bg-darkgray: rgb(39, 39, 39);
    --bg-lightsilver: #fafafa;
    --bg-lightersilver: #ddd;
    /* --bg- */
    --bg-silver: #ccc;

    --c-darkgray: rgb(37, 37, 37);

    --fs-1: 2rem;
    --fs-2: 1.4rem;
    --fs-3: 1rem;
    --fs-4: 0.91rem;
    --fs-5: 0.57rem;

    --slider-height: calc(100vh - 140px);
}

/* FONTS */
/* BPG Banner */
/* BPG Banner Caps */
/* BPG Glaho WEB Caps */
/* FONTS */

@media screen and (max-width: 1366px) {
    :root {
        --fs-1: 1.6rem;
        --fs-2: 1.06rem;
        --fs-3: 0.83rem;
        --fs-4: 0.76rem;
        --fs-5: 0.46rem;

        --slider-height: calc(100vh - 140px);
    }
}

body {
    background-color: #efefef;
}

body::-webkit-scrollbar {
    width: 10px;
    display: none;
}

a {
    text-decoration: none;
}

/* TOP-HEADER */
.top-header {
    width: 100%;
    height: 50px;
    display: flex;
    align-items: center;
    background-color: black;
    color: #eee;
    padding: 0 50px;
    position: sticky;
    top: 0px;
    z-index: 4;
}

/* ------------------ WEBICONS START */
.web-icons {
    min-width: 20%;
    height: 75%;
    display: flex;
    align-items: center;
    justify-content: end;
    border-right: 1px solid var(--bg-darkgray);
    position: relative;
}

.web-icons ul li {
    list-style-type: none;
    margin-right: 30px;
    display: inline-block;
}

.web-icons ul li a {
    font-size: 1.3rem;
    color: white;
}

/* ----------------- WEBICONS END */

/* CONTACT START */
.th-contact {
    min-width: 15%;
    height: 75%;
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 0 20px;
    border-right: 1px solid var(--bg-darkgray);
}

.th-contact a {
    color: white;
    font-size: 1.3rem;
    display: flex;
    align-items: center;
}

.th-contact a span {
    font-size: var(--fs-3);
    margin-left: 20px;
    font-family: "bpg banner";
}

/* CONTACT END */

/* LANGUAGE START */

.th-lang {
    width: 50px;
    height: 75%;
    margin-left: auto;
    border-left: 1px solid var(--bg-darkgray);
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
}

.th-lang ul {
    list-style-type: none;
}

.primary-lang {
    padding: 0 10px 0px 10px;
}

.lang-menu {
    position: absolute;
    display: none;
    background-color: #000;
    padding: 10px;
    border-radius: 0 0 5px 5px;
}

.th-lang img,
.lang-menu li img {
    width: 32px;
}

.th-lang:hover .lang-menu {
    display: block;
}

/* LANGUAGE END */
/* TOP-HEADER END */

/* HEADER START */
.header {
    width: 100%;
    height: 90px;
    background-color: var(--bg-lightsilver);
    display: flex;
    align-items: center;
    padding: 0 20px 0 50px;
    position: sticky;
    top: 50px;
    z-index: 3;
    user-select: none;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.4);
}

/* HEADER LOGO START */
.header-logo {
    min-width: 180px;
    /* width: 20%; */
    height: 100%;
    padding: 10px 5px;
}

.header-logo a p {
    letter-spacing: 3px;
    color: var(--c-darkgray);
    font-family: "Chakra Petch", sans-serif;
    font-weight: bold;
    display: flex;
    align-items: center;
    margin-left: -8px;
    font-size: var(--fs-4);
}

.header-logo a p span {
    padding: 3px 1px;
    background-color: #333;
    color: white;
    border-radius: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
    padding-left: 4px;
    margin-right: 3px;
    margin-left: 5px;
}

.header-logo img {
    height: 70%;
}

/* HEDAER LOGO END */

/* NAVBAR MENU START */

.navbar {
    height: 100%;
    display: flex;
    align-items: center;
    margin-left: auto;
    margin-right: auto;
    cursor: default;
    color: var(--c-darkgray);
}

.navbar ul {
    list-style-type: none;
}

.navbar ul li {
    display: inline-block;
    margin-left: 40px;
    font-family: "bpg banner caps";
    font-size: var(--fs-2);
}

.navbar ul li a {
    color: var(--bg-darkgray);
}

/* NAVBAR DROPDOWN START */

.dropdown span {
    display: flex;
    align-items: center;
}

.dropdown span::after {
    content: "⌵";
    margin-left: 5px;
    margin-bottom: 5px;
}

.dropdown-menu {
    min-width: 300px;
    position: absolute;
    display: none;
}

.dropdown-menu ul {
    width: 100%;
    display: block;
    margin-top: 40px;
    background-color: var(--bg-lightsilver);
    box-shadow: -2px 2px 5px rgba(0, 0, 0, 0.4);
}

.dropdown-menu ul a li {
    display: block;
    margin: 0 !important;
    padding: 20px;
    transition: 0.3s;
    -webkit-transition: 0.3s;
    font-size: var(--fs-3);
}

.dropdown-menu ul li:hover {
    background-color: var(--bg-lightersilver);
}

.dropdown-menu ul li::after {
    /* content: ' ›'; */
    font-size: var(--fs-2);
}

.dropdown:hover .dropdown-menu {
    display: block;
}

/* NAVBAR DROPDOWN END */

/* NAVBAR DROPDOWN SUB START */

